const Img=styled.img`
  width:80%;

`
const ButtonWrap=styled.div`
  width:100%;
  max-width:420px;
  height:70px;
  position:fixed;
  bottom:0px;
  left:50%;
  transform:translateX(-50%); 
`

const rotator = keyframes`
  0% {
    -webkit-transform: rotate(-45deg) translateZ(0);
    transform: rotate(-45deg) translateZ(0);
  }
  100% {
    -webkit-transform: rotate(315deg) translateZ(0);
    transform: rotate(315deg) translateZ(0);
  }
`;
const Loading=styled.div`
  position:absolute;
  left:50%;
  top:50%;
  transform:translate(-50%,-50%);
  img{
    text-align:center;
    widht:70px;
    height:70px;
    animation-name: ${rotator};
    animation-iteration-count: infinite;
    animation-duration: 3s;
  }
  div{
    margin:20px;
  }
  div:nth-child(3){
    font-size:8px;
  }
`;

function Faceshape() {
  const [image, setImage] = useState('');
  const [isDone,setIsDone]=useState('');
  const [isUpload,setIsUpload]=useState('');
  const [character,setCharacter]=useState({cheek:'',jaw:'',aspect:''});
  let inputRef;
  const [isSend,setIsSend]=useState('');
  const [cookies, setCookie, removeCookie] = useCookies(['rememberText','rememberCheek','rememberJaw','rememberAspectratio','rememberImg']);
  const cardRef=useRef();

  useEffect(()=>{
    if(cookies.rememberText!== undefined){
      setIsDone(cookies.rememberText); 
    }
  },[cookies.rememberText])
  useEffect(() => {
    if(cookies.rememberCheek!== undefined){
      setCharacter({cheek:cookies.rememberCheek,jaw:cookies.rememberJaw,aspect:cookies.rememberAspectratio})
    }
  },[cookies.rememberCheek,cookies.rememberJaw,cookies.rememberAspectratio]);
  function convertBase64(file){
    return new Promise((resolve,reject)=>{
      const fileReader=new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload=()=>{
        resolve(fileReader.result);
      }
      fileReader.onerror=(error)=>{
        reject(error);
      }
    })
  }
  useEffect(()=>{

  })
  const sendImageToServer = async (e) => {
    window.scrollTo(0, 0);
    e.target.disabled = true;
    const base64=await convertBase64(image)
    const data = [{'image': base64.split(',')[1]}];
    setIsSend('true')
    await axios({
      url:'https://hanjoongyoo.pythonanywhere.com/dlib/test',
      method:"POST",
      data:data,
      contentType:'application/json; charset=UTF-8',
    })
    .then(function(res){
      setCharacter({cheek:res.data.cheekSize,jaw:res.data.jawSize,aspect:res.data.aspectRatio})
      alert("진단서가 도착했어요!");
      setIsDone("Done");
      setCookie('rememberText', 'Done', '1');
      setCookie('rememberCheek', res.data.cheekSize, '1');
      setCookie('rememberJaw', res.data.jawSize, '1');
      setCookie('rememberAspectratio', res.data.aspectRatio, '1');
    })
    .catch(error=>{
      console.log('error:',error.response)
      alert("사진을 다시 찍어주세요!")
      setIsUpload('')
      setImage('');
      setIsSend('')
      e.target.disabled = false;
    })

    setImage("");
    window.scrollTo(0, 0);
  }

  const saveImage = (e) => {
    e.preventDefault();

    window.scrollTo(0, 0);
    const fileReader = new FileReader();
    if(e.target.files[0]){
      fileReader.readAsDataURL(e.target.files[0])
      setImage(e.target.files[0]);
      setIsUpload('Done')
    }else{
      alert('사진을 다시 찍어주세요')
    }  
  }
  const handleClick=()=>{
    setIsDone('');
    setIsUpload('');
    setIsSend('');
    setCharacter('');
    setImage('');
    removeCookie('rememberText');
    removeCookie('rememberCheek');
    removeCookie('rememberJaw');
    removeCookie('rememberAspectratio');
    removeCookie('rememberImg');
    clearInterval();
    window.scrollTo(0, 0);
  }
  // const onDownloadBtn=()=>{
  //   const card = cardRef;
  //   const scale=2;
  //   const options={
  //     width: card.clientWidth * scale,
  //     height: card.clientHeight * scale,
  //     style: {
  //       transform: 'scale('+scale+')',
  //       transformOrigin: 'top left'
  //   }}
  //   domtoimage
  //     .toBlob(card,options)
  //     .then((blob) => {
  //       saveAs(blob, '얼굴형 진단서.png');
  //     });
  // }
  return(
    <>
        {isDone==="Done"?<Wrap><div ref={cardRef}><FaceDiagnosis character={character}></FaceDiagnosis></div><ButtonWrap><Button onClick={handleClick}>다시 진단하기</Button></ButtonWrap></Wrap>:
        <Wrap>
          <input type="file" accept="image/*"
            onChange={saveImage}
            ref={refParam => inputRef = refParam}
            style={{ display: "none" }}
          />
          {isUpload==="Done"?
            <>
              {isSend?
                  <Loading>
                    <img alt="img" src={process.env.PUBLIC_URL + `/img/loading.png`}></img>
                    <div>진단중입니다</div>
                    <div>기다려주세요</div>
                  </Loading>
                :
                <>
                  <div style={{lineHeight:'160%',position:'absolute',left:'50%',top:'50%',transform:'translate(-50%,-50%)',fontSize:'16px',width:'100%'}}>❗️사진은 저장되지 않아요❗️<br/>업로드를 누르면 사진을 보냅니다</div>
                  <ButtonWrap><Button onClick={sendImageToServer}>업로드</Button></ButtonWrap>
                </>
                }
            </>
            :
            <>
              <Img alt="img" src={process.env.PUBLIC_URL + "/img/diagnosis.png"} />
              <Description>정면으로 찍은 원본 사진을 올려주세요<br/>위와 같은 결과물을 얻을 수 있습니다</Description>
              <ButtonWrap>
                <Button type="primary" onClick={()=>inputRef.click()}>
                  얼굴형 진단서 받기
                </Button>
              </ButtonWrap>
            </>
          }
        </Wrap>}
    </>
  )
}

export default Faceshape;